












































    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../login/login',
                    });
                }
            },
			gotoRecord:function(e){
				uni.navigateTo({
					url: '../check-record/check-record'
				})
			},
			gotoCheckPlan:function(e){
				uni.navigateTo({
					url: '../ckeck-plan/ckeck-plan'
				})
			},
			gotoAnswetList:function(e){
				uni.navigateTo({
					url: '../answer-list/answer-list'
				})
			},
			gotoQuestion:function(e){
				uni.navigateTo({
					url: '../check-question/check-question'
				})
			}
        }
    }
